var frames = document.querySelectorAll('.frame-section');
var framesLength = frames.length
var arrayFrames = Array.prototype.slice.call(frames);

console.log("array frames", arrayFrames);

var zSpacing = 500; 
var triggers = createTriggers(framesLength);
var originalZValues = [0, -500, -1000];
//var originalZValues = createOriginalZvalues(framesLength, zSpacing);
var opacityValues = [0, 1]


gsap.registerPlugin(ScrollTrigger);


for(var i = 0; i < triggers.length; i++){
    var currentTrigger = triggers[i];
    var initialZvalues = []
    var finalZvalues = []
    //fill initial z values
    for(var w=0; w<originalZValues.length; w++){
        initialZvalues.push( originalZValues[w] + (500 * i ));
    }
    console.log("initial z values", initialZvalues);
    //fill final z values
    for(var w=0; w<originalZValues.length; w++){
        finalZvalues.push( originalZValues[w] + (500 * (i + 1)));
    }
    console.log("final z values", finalZvalues);

    var tl = gsap.timeline({
        onComplete: function(){ console.log("TERMINOOOOOOO") },
        scrollTrigger: {
            trigger: currentTrigger,
            start: 'top 50%',
            end: 'bottom 40%',
            scrub: false,
            // onEnter onLeave onEnterBack onLeaveBack
            toggleActions: "play none reverse none",
            markers: {
                fontSize: '2rem'
            }
        }
    });

    for(var j = 0; j < originalZValues.length; j++){

        var initialValue = initialZvalues[j];
        var finalValue = finalZvalues[j];

        var initialOpacity = undefined; 
        var finalOpacity = undefined;

        if (finalValue === 500){
            initialOpacity = 1
            finalOpacity = 0
        }
        if(finalValue === 0){
            initialOpacity = 0
            finalOpacity = 1
        }
    
        tl.fromTo(arrayFrames[j], {
            z: initialValue,
            duration: 3,
            opacity: initialOpacity
        }, {
            z: finalValue,
            duration: 3,
            opacity: finalOpacity
        });
        console.log("Creado asi ----")
        console.log("Frame", arrayFrames[j]);
        console.log("initial Z value", initialValue)
        console.log("final  Z value", finalValue)
        console.log("Trigger", currentTrigger);
        console.log("end -----")        
    }
    console.log("NEXT TRIGGER ---------------");
}



function createTriggers(numFrames){
    var triggers = []

    if(numFrames == 1 ){
        return []
    }

    if(numFrames == 2 ){        
        return [".trigger1"]
    }

    for(var i=0; i<numFrames-1; i++){
        var trigger = ".trigger"
        var triggerNum = i + 1;
        triggers.push( trigger + triggerNum.toString())
    }
    console.log("Created triggers", triggers);
    return triggers;
}

function createOriginalZvalues(numFrames, zSpacing){
    var zValues = []
    for(var i=0; i<numFrames;i++) { 
        zValues.push( zSpacing * -i);
    }
    console.log("Created Z Values", zValues);
    return zValues;
}


